d-sec {\n    display: flex;\n    justify-content: space-between;\n    margin: 5em auto;\n}\n.third-sec h1 {\n    font-family: \"Poppins\";\n    font-style: normal;\n    font-weight: 500;\n    font-size: 26.7843px;\n    line-height: 40px;\n    letter-spacing: -0.36524px;\n    color: #000000;\n}\n.third-sec h3 {\n    font-family: \"Poppins\";\n    font-style: normal;\n    font-weight: 400;\n    font-size: 13.9348px;\n    line-height: 21px;\n    color: #000000;\n}\n.third-sec form {\n    display: flex;\n}\n.third-sec form input {\n    border: none;\n    border-bottom: 2px solid #000000;\n    background-color: #ffffff !important;\n    height: 44px;\n}\n.third-sec form input::placeholder {\n    font-family: \"DM Sans\";\n    font-style: normal;\n    font-weight: 400;\n    font-size: 13.3333px;\n    line-height: 17px;\n    /* identical to box height */\n\n    letter-spacing: -0.181818px;\n\n    color: #000000;\n\n    mix-blend-mode: normal;\n    opacity: 0.5;\n}\n.third-sec > div:nth-child(2) > div {\n    font-family: \"Poppins\";\n    font-style: normal;\n    font-weight: 400;\n    font-size: 9.29915px;\n    line-height: 14px;\n    letter-spacing: -0.126807px;\n\n    color: #959595;\n}\n.for-business > h3 {\n    font-family: \"Poppins\";\n    font-style: normal;\n    font-weight: 600;\n    font-size: 32.8942px;\n    line-height: 49px;\n    color: #c4456f;\n    margin-bottom: 10px;\n}\n.for-business > h2 {\n    font-family: \"Poppins\";\n    font-style: normal;\n    font-weight: 600;\n    font-size: 17.1627px;\n    line-height: 26px;\n    color: #c4456f;\n    margin-bottom: 20px;\n}\n.for-business > a {\n    display: block;\n    text-decoration: none;\n    border: 1.14371px solid #d6d5d5;\n    border-radius: 6.33127px;\n    padding: 6px 21px;\n    overflow-x: hidden;\n    font-family: \"Segoe UI\";\n    font-style: normal;\n    font-weight: 600;\n    font-size: 12.9878px;\n    line-height: 17px;\n    text-transform: capitalize;\n    color: #636363;\n    margin-bottom: 7px;\n    width: max-content;\n}\n.for-business > a.active {\n    background: #c4456f;\n    color: #ffffff;\n}\n@media only screen and (max-width: 576px) {\n    .second-sec {\n        flex-wrap: wrap;\n    }\n    .second-sec > div:nth-child(1) {\n        width: 100%;\n        padding: 10px;\n    }\n    .second-sec > div:nth-child(2) {\n        display: none;\n    }\n    .second-sec > .for-business {\n        display: block !important;\n        width: 100%;\n    }\n    .first-sec {\n        justify-content: center;\n    }\n    .first-sec > div:nth-child(1) {\n        text-align: center;\n    }\n    .first-sec > .for-business {\n        display: none;\n    }\n}\n</style>\n"],"sourceRoot":""}]);
